
.view-member-family {
  xposition: relative;
}
.view-member-family .weui-dialog {
  min-height: 1.85185rem;
  /*max-height: 900px;*/
  height: 50%;
  overflow: scroll;
  background: #fff;
}
.view-member-family li {
  border-bottom: 0.01852rem solid #9a9a9a;
  padding: 0.37037rem 0;
}
.view-member-family li:last-child {
  border: none;
}
.view-member-family li .common-box-list .img {
  display: inline-block;
}
.view-member-family li .common-box-list .text {
  display: inline-block;
  width: 1.38889rem;
  text-align: left;
}



